import React, { useState } from 'react';
import { ActionBtn } from './buttons';

const ParentTweet = props => {
	const { tweet } = props;
	return tweet.parent ? <div><Tweet hideActions tweet={tweet.parent} /></div> : null;
}

const Tweet = function (props) {
	const { tweet, hideActions } = props;
	const [actionTweet, setActionTweet] = useState(props.tweet ? props.tweet : null);
	const path = window.location.pathname;
	const match = path.match(/(?<tweetid>\d+)/);
	const urlTweetId = match ? match.groups.tweetid : -1;
	const isDetail = `${tweet.id}` === `${urlTweetId}`;
	

	const handleLink = (event) => {
		event.preventDefault();
		window.location.href = `/${tweet.id}`
	}
	const handlePerformAction = (newActionTweet, status) => {
		if (status === 200) {
			setActionTweet(newActionTweet);
		} else if (status === 201) {
			// let the tweet list know
			if (props.didRetweet) {
				setActionTweet(newActionTweet);
				props.updateTweetsDidSet(false); // callback to setTweetsDidSet

			}
		}
	}
	return (
		<div className="col-sm-6 mx-auto border rounded py-3 mb-4">
			<div>
				<p>{tweet.content}</p>
				<ParentTweet tweet={tweet} />
			</div>
			<div className="btn btn-group">
			{(actionTweet && hideActions !== true) && <React.Fragment>
				<ActionBtn
					tweet={actionTweet}
					didPerformAction={handlePerformAction}
					action={{ type: 'like', display: 'Like(s)' }}
					className=" btn-primary">
				</ActionBtn>
				<ActionBtn
					tweet={actionTweet}
					didPerformAction={handlePerformAction}
					action={{ type: 'unlike', display: 'Unlike' }}
					className=" btn-secondary">
				</ActionBtn>
				<ActionBtn
					tweet={actionTweet}
					didPerformAction={handlePerformAction}
					action={{ type: 'retweet', display: 'Retweet' }}
					className=" btn-outline-dark"
				>
				</ActionBtn>
			</React.Fragment> 
			}
				{ !isDetail && <button className="btn btn-sm btn-success" onClick={handleLink}>View</button>}
			</div>
		</div>
	)
}



export {
	ParentTweet,
	Tweet
}